
import { defineComponent, ref, watch } from "vue";
import { useRouter } from "vue-router";
import Calendar from "../calendar/Calendar.vue";
import NavBar from "./NavBar.vue";
import { ZTCalendarConfig } from "../calendar/calendar.config";
import dayjs from "dayjs";
export default defineComponent({
    components: {
        Calendar,
        NavBar,
    },
    setup() {
        const router = useRouter();
        const backAction = () => {
            router.replace({
                name: "Index",
                params: {
                    mode: "pop",
                },
            });
        };

        let selectedDates = ref<Array<string>>([]);
        if (router.currentRoute.value.query.selectedDate) {
            selectedDates.value = [router.currentRoute.value.query.selectedDate as string];
        }
        watch(selectedDates, (newVal) => {
            if (newVal.length) localStorage.setItem("CalendarSelect2", newVal[0]);
            else localStorage.setItem("CalendarSelect2", "");
            backAction();
        });

        const config = new ZTCalendarConfig();
        config.canUseDays = 19;
        config.startDay = dayjs().add(3, "day").format("YYYY-MM-DD");
        console.log("startDay:", config.startDay);

        return {
            backAction,
            calendarConfig: ref(config),
            selectedDates,
        };
    },
});
